import {sound} from "./sound/sound";
import {showWarnBtn} from "./warn-btn";
const  playInit = ()=>{
    const playBtn = document.querySelector('.preloader__play')
    function play(){
        playStartSound()
        showWarnBtn()
        hidePreloader()
    }
    function hidePreloader(){
        let preloader = document.querySelector('#preloader')
        preloader.classList.add('hide')
    }
    function playStartSound() {
        sound.playAudio({ audioName: 'вступление', volume: 0.2, isLoop: true })
    }
    playBtn.addEventListener('click',play,{once:true})
}
export default playInit
