import { BatchRenderer, extensions as Extensions, Renderer } from "@pixi/core";
import { Container } from "@pixi/display";
Extensions.add(BatchRenderer);

export default class Application {
  /** DOM элемент */
  view = document.createElement("canvas");
  stage = new Container();
  renderer;

  constructor(width = 16, height = width) {
    this.renderer = new Renderer({
      width,
      height,
      view: this.view,
      backgroundAlpha: 0, // раскоменти, чтобы убрать черный фон за спайном
    });
  }

  /** вызывается каждый кадр, рисует содержимое приложения во view */
  render() {
    this.renderer.render(this.stage);
  }
}
