// styles
import './../styles/style.scss'

import Dialog from './dialog.js'
// Scene create
import {SceneCreater} from './scene-creater.js'
// resolution
import resolution from './screenResolution.js'
// sound
import soundAction from './sound/soundAction.js'
//fullscreen
import fullscreen from './fullscreen.js'
// Preloader
import Preloader from './preloader.js'
// Typed
import TalkUpdate from './talk-updateer.js'
// Warning
import {Warning} from './warn-btn.js'
// SceneSetting
import SceneSetting from './sceneSettings.js'
// spine
import {spineManager} from './spineManager.js'
// initGameMechanics
import {initGameMechanics} from './gameMechanics'
// button
import button from './change-btns'
// all scene count
import {
    allScenesCreate,
    createSexScene,
    resetSceneCount,
    sceneCount,
    setSceneCount,
} from './allScene'
// edit scene functions
import {
    animScene,
    changeImg,
    fade,
    hideGameInterface,
    hideTalkEl,
    removeScene,
    removeScenes,
} from './editFunctions'
import {endScreen} from './endScreen'
import {sound} from './sound/sound'
import playInit from "./play-btn";
//Main script
soundAction()
fullscreen()
resolution()

SceneCreater(Preloader)
Warning(gameInit)
playInit()

let nowScene     = 1,
    talkIndex    = 0
let nowTalk      = getDialog(nowScene, talkIndex)
let nowSetting   = SceneSetting[nowScene - 1]
let modifyScene  = nowSetting.modifyScene
let soundScene   = nowSetting.sound
let isGameScene  = nowSetting.gameScene
let isReplayPose = false

function gameInit() {
    removeScenes(nowScene) //для тестов
    spineManager.checkScene(nowScene)
    button.clickCallback(changeScene)
    fade(1000, true)
    setSetting()
}

function setSound(soundSceneSetting) {
    let playSoundsArr  = soundSceneSetting.play
    let pauseSoundsArr = soundSceneSetting.pause
    if (soundSceneSetting.chain) {
        playChainSounds(playSoundsArr, 0, soundSceneSetting.nextScene)
        return
    }

    if (playSoundsArr) playSounds(playSoundsArr)
    if (pauseSoundsArr) pauseSounds(pauseSoundsArr)
}

function playChainSounds(soundArr, index = 0, next) {
    if (!soundArr[index]) {
        if (next) {
            updateSettings()
            updateScene()

            TalkUpdate(nowTalk, ...getSettingForThisTalk(nowScene, talkIndex))


            setSetting()
        }
        return
    }
    let audio = sound.playAudio(soundArr[index])
    audio.addEventListener('ended', () => {
        playChainSounds(soundArr, index + 1, next)
    })
}

function setSoundTalk(soundTalk) {
    if (soundTalk) setSound(soundTalk)
}

function changeScene() {
    talkIndex++

    nowTalk       = getDialog(nowScene, talkIndex)
    let soundTalk = getSoundForThisTalk(nowScene, talkIndex)
    setSoundTalk(soundTalk)
    if (nowTalk !== undefined) {
        TalkUpdate(nowTalk, ...getSettingForThisTalk(nowScene, talkIndex))
    } else {
        // change scene
        if (sceneCount === nowScene + 1) {
            endScreen.showEndScreen()
            button.hideBtn()
            nowScene = 'end'
            pauseSoundAtEnd()
            hideTalkEl()
            spineManager.checkScene(nowScene)
            return
        } else if (isReplayPose) {
            endScreen.showEndScreen()
            hideGameInterface()
            button.hideBtn()
            nowScene = 'end'
            spineManager.checkScene(nowScene)
            return
        }

        updateSettings()

        if (isGameScene) {
            button.hideBtn()
            hideTalkEl()
            setTimeout(() => {
                updateScene()
                initGameMechanics(nowScene)
            }, nowSetting.gameDelayUpdate)

            if (nowSetting.gameFade) fade(nowSetting.gameFade)
        } else if (modifyScene) {
            addModifyAndUpdateScene(modifyScene)
            button.hideBtn()
            hideTalkEl()
        } else {
            updateScene()
            TalkUpdate(nowTalk, ...getSettingForThisTalk(nowScene, talkIndex))
        }
        if (soundScene) setSound(soundScene)
    }
    if (nowTalk && !getDialog(nowScene, talkIndex + 1)) {
        if (modifyScene?.btnEnd) button.changeBtn(modifyScene.btnEnd)
    }
}

function updateScene() {
    removeScene(nowScene)
    nowScene++
    spineManager.checkScene(nowScene)
    talkIndex = 0
    nowTalk   = getDialog(nowScene, talkIndex)
}

function setSetting() {

    if (modifyScene) modify(modifyScene)
    if (soundScene) setSound(soundScene)
    if (isGameScene) {
        button.hideBtn()
        initGameMechanics(nowScene)
    }
}

function updateSettings() {
    nowSetting  = SceneSetting[nowScene]
    isGameScene = nowSetting.gameScene

    modifyScene = nowSetting.modifyScene
    soundScene  = nowSetting.sound
}

function addModifyAndUpdateScene(modifyObj) {
    if (modifyScene.delayUpdate) {
        setTimeout(() => {
            updateScene()
        }, modifyScene.delayUpdate)
    } else {
        updateScene()
    }
    modify(modifyObj)
}

function modify(modifyObj) {
    if (modifyObj.fadeStart) {
        button.hideBtn()
        fade(modifyObj.fadeStart)
    }
    if (modifyObj.anim) {
        modifyObj.anim.forEach(className => {
            animScene(className)
        })
    }
    if (modifyObj.imgChange) {
        changeImg(modifyObj.imgChange)
    }
    if (modifyObj.next) {
        if (modifyObj.fadeNext) {
            setTimeout(() => {
                fade(modifyObj.fadeNext)
            }, modifyObj.delayNext - modifyObj.fadeNext)
        }
        setTimeout(() => {
            updateScene()
            let btn = SceneSetting[nowScene - 1]?.modifyScene?.btn

            if (btn) {
                button.changeBtn(btn)
            }
            TalkUpdate(nowTalk, ...getSettingForThisTalk(nowScene, talkIndex))
        }, modifyObj.delayNext)
    } else {
        setTimeout(() => {
            TalkUpdate(nowTalk, ...getSettingForThisTalk(nowScene, talkIndex))
        }, modifyScene.delayTalk)
    }

    if (modifyObj.btn && !modifyObj.next) {
        button.changeBtn(modifyObj.btn)
    }
    if (modifyObj.gameEnd) {
        hideGameInterface()
    }
}

function getDialog(idScene, indexTalk) {

    return Dialog[idScene - 1][indexTalk]
}

function getSettingForThisTalk(idScene, indexTalk) {
    let talkBabelSetting = SceneSetting[idScene - 1][`talk${indexTalk + 1}`]
    return talkBabelSetting
        ? [talkBabelSetting.top, talkBabelSetting.left, talkBabelSetting.type]
        : []
}

function replayGame() {
    nowScene    = 1
    talkIndex   = 0
    nowTalk     = null
    nowSetting  = SceneSetting[nowScene - 1]
    modifyScene = nowSetting.modifyScene
    button.showBtn()
    removeScenes(sceneCount)
    hideTalkEl()
    resetSceneCount()
    allScenesCreate()

    spineManager.checkScene('end')
    spineManager.checkScene(nowScene)
    isReplayPose = false
    hideGameInterface()
    sound.pauseAudio({audioName: 'экшн'})
    sound.playAudio({audioName: 'вступление', volume: 0.2, isLoop: true})

    fade(1000, true)
}

function replayPose(scene) {
    nowScene = scene
    button.hideBtn()
    removeScenes(sceneCount)
    hideTalkEl()
    setSceneCount(scene)
    createSexScene(nowScene)

    spineManager.checkScene('end')

    spineManager.checkScene(nowScene)
    initGameMechanics(nowScene)
    sound.pauseAudio({audioName: 'экшн'})
    sound.pauseAudio({audioName: 'вступление'})
    sound.playAudio({audioName: 'экшн', volume: 0.025, isLoop: true})
    isReplayPose = true
}

function getSoundForThisTalk(idScene, indexTalk) {
    let getSoundSetting
    getSoundSetting = SceneSetting[idScene - 1][`talk${indexTalk + 1}`]?.sound
    return getSoundSetting
}

function playSounds(audioPlaySettingArr) {
    audioPlaySettingArr.forEach(audioSetting => sound.playAudio(audioSetting))
}

function pauseSounds(audioPauseSettingArr) {
    audioPauseSettingArr.forEach(audioSetting => sound.pauseAudio(audioSetting))
}

function pauseSoundAtEnd() {
    sound.pauseAudio({audioName: 'капли в пещере'})
    // sound.pauseAudio({ audioName: 'экшн' })
    // sound.pauseAudio({audioName: 'вступление'})
}

export {replayGame, replayPose}
