import button from "./change-btns";
let isKeyListener = (v) => {
  islisten = v;
};
let islisten = true;
window.addEventListener("keydown", keyDown);
function keyDown(e) {
  if (!islisten) return;

  if (e.code == "Space" || e.code == "ArrowRight") {
    button.activeBtn.click();
  }
}

export default isKeyListener;
