import keyListener from './keyListener.js'
let continueBtn = document.querySelector('.continue-btn')
let nextBtn = document.querySelector('.next-btn')

const button = {
	btnBox: document.querySelector('#change-btn'),
	activeBtn: nextBtn,
	hideBtn,
	showBtn,
	changeBtn,
	clickCallback,
}

function hideBtn() {
	keyListener(false)
	button.btnBox.classList.add('hide')
	button.activeBtn.disabled = true
}
function showBtn() {
	keyListener(true)
	button.btnBox.classList.remove('hide')
	button.activeBtn.disabled = false
}
function changeBtn(btnType) {
	if (btnType === 'continue' && button.activeBtn != continueBtn) {
		button.activeBtn = continueBtn
		button.btnBox.classList.remove('next')
		setTimeout(() => button.btnBox.classList.add('continue'), 200)
	} else if (btnType === 'next' && button.activeBtn != nextBtn) {
		button.activeBtn = nextBtn
		button.btnBox.classList.remove('continue')
		setTimeout(() => button.btnBox.classList.add('next'), 200)
	}
}
function clickCallback(func) {
	continueBtn.addEventListener('click', () => {
		if (button.activeBtn == continueBtn) func()
	})
	nextBtn.addEventListener('click', () => {
		if (button.activeBtn == nextBtn) func()
	})
}
export default button
