const adaptive = () => {
	let spineScenes = document.querySelectorAll('.spine-scene')
	setScale()
	window.addEventListener('resize', setScale)
	function setScale() {
		spineScenes.forEach(spineScene => {
			spineScene.style.transform = `scale(${(
				getContainerWidth() / 1350
			).toFixed(2)})`
		})
		setTalkSize()
	}
	function getContainerWidth() {
		let containerWidth = document
			.querySelector('#container')
			.getBoundingClientRect().width
		return containerWidth
	}
	function setTalkSize() {
		let talkBlock = document.querySelector('.talk')
		talkBlock.style.fontSize = 30 * (getContainerWidth() / 1920) + 'px'
	}
}
export default adaptive
